.footer {
    background-color: #000000;
    min-height: 30vh;
    padding-bottom: 30px;
    padding-top: 30px;
    display: flex;
    align-items: center;

    &-border {
        height: 100%;
        width: 100%;
        border-top: 3px solid $color-estetic;
    }


    &-title {
        text-align: center;
        height: 100%;
        padding-top: 15%;
        color: #ffffff;
        font-family: $ff-barcode, cursive;
        font-size: 40px;

        @include _md {
            font-size: 25px;
            padding-top: 5%;
        }

        @include _lg {
            padding-top: 5%;
        }
    }

    &-text {
        border-bottom: 1px solid $color-estetic;
        padding-bottom: 20px;

        .logo-text {
            display: flex;
        }

        &-content {
            color: $color-grey;
            font-size: 16px;
            font-family: $ff-geo, serif;
        }
    }
}