.about-me {
    position: relative;
    position: relative;
    background: url('https://media.istockphoto.com/photos/unique-design-abstract-digital-pixel-noise-glitch-error-video-damage-picture-id853863100?b=1&k=6&m=853863100&s=170667a&w=0&h=dKv-FoEHiX7untBDFPMlQ8sfcVGd_HUbcDAcmBogkkw=')  center center / cover;
    background-attachment: fixed;

    @include _lg {
        background-attachment: unset;
    }

    &-bg {

        .planet{
            width: 300px;
            height: 300px;
            position:absolute;
            border-radius:50%;
            overflow:hidden;
            box-shadow: 0 0 60px -20px rgba(255, 189, 3, 0.72), -14px -15px 40px -10px rgba(255, 238, 191, 0.23);
            left:50%;
            top:50%;
            margin:-150px;
          }
          .planet .mask{
            width:100%;
            height:100%;
            position: absolute;
            box-shadow:inset -10px -10px 40px #251303, inset 10px 10px 30px -10px rgba(255, 204, 159, 0.6);
            border-radius:50%;
          }
          .planet .background{
            animation: translateBackground 40s infinite linear;  
            background:url(http://artem.anmedio.ru/dev/planet/mars.jpg) repeat-x;
            width: 300px;
            height: 300px;
            position:absolute;
            border-radius:50%;

            @include _lg {
                animation: none;
            }
          }
          .planet .clouds{
            background:url(http://artem.anmedio.ru/dev/planet/clouds.png) repeat-x;
            width: 300px;
            height: 300px;
            position:absolute;
            border-radius:50%;
            animation: translateBackground 30s infinite linear;
            opacity: 0.4;

            @include _lg {
                animation: none;
            }
          }
          .planet .wrap{
            width: 300px;
            height: 300px;
            position:absolute;
            border-radius:50%;
            animation: rotatePlanet 150s infinite linear;
            
            @include _lg {
                animation: none;
            }
          }
          @keyframes translateBackground{
            0%{
              background-position-x:0;
            }
            100%{
              background-position-x:-600px;
            }
          }
          @keyframes rotatePlanet{
            0%{
              transform:rotateZ(0);
            }
            100%{
              transform:rotateZ(-360deg);
            }
          }
          
    }
    
    #red {
        fill: none;
        opacity: 0.15;
        stroke: $color-estetic;
        stroke-width: 12;
        stroke-miterlimit:10;
        animation: show 4s;
        animation-fill-mode: forwards;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out; 
      }
      
      #blue {
        fill: none;
        opacity: 0.15;
        stroke: #0058bd;
        stroke-width: 12;
        stroke-miterlimit:10;
        animation: show 4s;
        animation-fill-mode: forwards;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
      }
      
      #light-blue {
        fill: none;
        opacity: 0.15;
        stroke: $color-grey;
        stroke-width: 6;
        stroke-miterlimit:10;
        stroke-dasharray: 200;
        stroke-dashoffset: 800;
        animation: draw 4s;
        animation-fill-mode: forwards;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;    
      }
      
      @keyframes draw {
        to {
          stroke-dashoffset: 0;
       }
          }
      
      @keyframes show {
        0% {
          opacity: 0.15;
       }
        50% {
          opacity: 0.2;
       }
        100% {
          opacity: 0.15;
       }
          }
      

    overflow-x: hidden;
    @import './AboutMeFlyingObjects.scss';

    text-align: center;
    padding-top: 75px;
    padding-bottom: 75px;
    background-size: contain;
    
    @keyframes text-movement1 {
        0% {
            transform: rotate(2deg);
        }

        50% {
            transform: rotate(-2deg);
        }

        100% {
            transform: rotate(2deg);
        }
    }
    @keyframes text-movement2 {
        0% {
            transform: rotate(-2deg);
        }

        50% {
            transform: rotate(2deg);
        }

        100% {
            transform: rotate(-2deg);
        }
    }

    &-title {
        font-family: $ff-2p, cursive;
        color: #ffffff;
        font-size: 60px;
        z-index: 4;

        animation: text-movement1 7s linear infinite;

        @include _lg {
            animation: none;
        }

        @include _sm {
            font-size: 40px;
        }
    }
    
    &-subtitle {
        font-size: 24px;
        color: #ffffff;
        font-family: $ff-2p, cursive;

        animation: text-movement2 7s linear infinite;

        @include _lg {
            animation: none;
        }

        @include _sm {
            font-size: 16px;
        }
    }

    @keyframes card-movement { 
        0% {
            transform: rotate(-3deg);
        }

        50% {
            transform: rotate(3deg);
        }

        100% {
            transform: rotate(-3deg);
        }
    }

    
    &-card {
        position: relative;
        animation: card-movement 20s linear infinite;

        @include _lg {
            animation: none;
        }

        .row {
            align-items: center;
        }
        
        box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 20px rgba(0, 0, 0, 0.1) inset;
            &:before, &:after{
                content:"";
                position:absolute;
                bottom:0;top:0px;left:0px;right:0px;
                z-index:1;
               -webkit-box-shadow:0 0 30px 2px $color-estetic;
                -moz-box-shadow:0 0 30px 2px $color-estetic;
                box-shadow: 0 0 30px 2px $color-estetic;
                border-radius: 20px;
              }


        &-background {
            border: 10px solid rgba(128, 128, 128, 0.548);
            background-color: rgba(247, 0, 255, 0.363);
            padding: 30px;
            border-radius: 20px;
        }

        &-information {
            width: 100%;
            text-align: left;
            min-height: 50vh;

            @include _sm {
                min-height: 40vh;
            }
        }

        &-list {
            padding: 10px;
            &-item {
                margin-bottom: 20px;
                font-family: $ff-2p, serif;
                color: #ffffff;
                font-size: 20px;

                @include _sm {
                    font-size: 12px;
                    margin-bottom: 5px;
                }
            }
        }

        &-photo {
            display: flex;
            justify-content: center;
            align-items: center;height: 100%;
            

            @include _600 {
                height: 150px;
            }
        }
    }


    &-form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        &-input {
            margin-bottom: 10px;
            background-color: rgba(0, 0, 0, 0);
            padding: 20px;
            outline: none;
            box-shadow: none;
            border: 5px solid rgba(255, 255, 255, 0.548);
            color: #ffffff;
            border-radius: 50%;

            &::placeholder {
                color: rgba(255, 255, 255, 0.404);
                font-size: 24px;
                font-family: $ff-geo, serif;
            }


            @include _sm {
                margin-bottom: 5px;
            }
        }

        &-submit {
            background-color: $color-estetic;
            color: #ffffff;
            border: 3px solid rgba(255, 255, 255, 0.548);
            padding: 20px 50px;
            border-radius: 50%;

            a {
                font-family: $ff-2p, serif;
                color: #ffffff;
            }
        }
    }

}
