.header {
    position: relative;
    overflow: hidden;

    &-content {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        padding-top: 50px;

        max-width: 100%;
        height: 70vh;
    }

    &-text {
        max-width: 80%;
        text-align: center;
        position: relative;

        padding-top: 50px;
        @include _md {
            padding-top: 0;
        }
    }

    &-title {
        font-size: 40px;
        margin-bottom: 16px;
        text-transform: uppercase;
        font-weight: 700;
        font-family: $ff-2p, cursive;
        color: #ffffff;

        @include _md {
            font-size: 20px;
        }
    }

    &-subtitle {
        font-family: $ff-2p, serif;
        color: #ffffff;
        font-size: 30px;

        @include _md {
            font-size: 15px;
        }

        @include _sm {
            font-size: 16px;
        }
    }

    @import "./navbar.scss";
}

.navbar-wrapper {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 20px;
    transition: 0.4s;

    @include _md {
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        height: 100vh;
        width: 40vw;
        background-color: #000000;
        z-index: 4;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        right: -50vw;
        transition: 0.4s;

        animation: disactivator_md 0.4s;
        animation-fill-mode: forwards;

        @keyframes disactivator_md {
            from {
                right: 0;
            }
            to {
                right: -50vw;
            }
        }

        @include _sm {
            width: 60vw;
            right: -80vw;

            animation: disactivator_sm 0.4s;
            animation-fill-mode: forwards;

            @keyframes disactivator_sm {
                from {
                    right: -20vw;
                }
                to {
                    right: -80vw;
                }
            }
        }

        padding: 20px 15px;

        &.active {
            animation: activator 0.4s;
            animation-fill-mode: forwards;

            @keyframes activator {
                to {
                    right: 0;
                }
            }
        }
    }
}

.navbar-list {
    display: flex;
    justify-content: center;
    align-items: center;

    @include _md {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-top: 50px;
    }
}

.navbar-list-item {
    color: $color-grey;
    font-size: 20px;
    font-family: $ff-2p, sans-serif;
    padding-left: 30px;

    @include _md {
        position: relative;
        font-size: 25px;
        font-size: 16px;
        padding-top: 20px;
        padding-bottom: 20px;
    }
}

.navbar-link {
    color: $color-grey;
}

.navbar-link:hover {
    color: $color-estetic;
    text-decoration: none;
}

[data-navbar-toggler] {
    // d:none
    display: inline-block;
    // ml:a
    margin-left: auto;

    @include _md {
        display: inline-block;
    }
}
