.home-whatido {
    padding-top: 30px;
    padding-bottom: 30px;
    min-height: 20vh;
    background: url('../../../media/photos/stars.svg') center center;
    background-attachment: fixed;
    background-color: #000000;

    @include _lg {
        background-attachment: unset;
    }

    .whatido-card {
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding-top: 30px;
        padding-bottom: 30px;
        min-height: 60vh;
        border-top: 5px solid $color-estetic;
        border-bottom: 5px solid $color-estetic;

        @include _md {
            min-height: 70vh;
        }

        @include _320 {
            min-height: 100vh;
        }

        &-icon {
            color: #ffffff;
            font-size: 120px;
            padding-bottom: 15px;

            @include _sm {
                font-size: 80px;
            }

            
        }

        &-subtitle {
            color: #ffffff;
            font-size: 50px;
            text-transform: uppercase;
            font-family:  $ff-monoton, cursive;

            @include _sm {
                font-size: 30px;
            }
        }

        &-text {
            color: #ffffff;
            font-size: 24px;
            font-family: $ff-audiowide, cursive;

            @include _sm {
                font-size: 16px;
            }
        }
    }
}