.works-project {
    background-color: #000000;
    min-height: 70vh;
    padding-top: 20px;
    display: flex;
    align-items: center;
    position: relative;
    
    .row {
        align-items: center;
    }

    &-img {
        width: 100%;
        border: 5px solid $color-estetic;
        border-radius: 15px;
        overflow: hidden;

        img {
            width: 100%;
        }
    }

    &-title {
        font-size: 40px;
        color: #ffffff;
        font-family: $ff-audiowide, cursive;
        text-align: center;
        padding-top: 15px;
    }

    &-text {
        text-align: center;
        font-family: $ff-audiowide, cursive;
        font-size: 14px;
        color: $color-grey;

        @include _md {
            font-size: 10px;
        }
    }

    .btn-go-back {
        position: absolute;
        top: 5%;
        left: 10%;

        @include _sm {
            font-size: 40px;
        }
        
        @include _md {
            top: 5%;
            left: 0;
        }
    }

}