@mixin _xl {
    @media (max-width: 1170px) {
        @content;
    }
}

@mixin _lg {
    @media (max-width: 979px) {
        @content;
    }
}

@mixin _md {
    @media (max-width: 767px) {
        @content;
    }
}

@mixin _600 {
    @media (max-width: 599px) {
        @content;
    }
}

@mixin _sm {
    @media (max-width: 480px) {
        @content;
    }
}

@mixin _320 {
    @media (max-width: 321px) {
        @content;
    }
}

@mixin _xs {
    @media (max-width: 361px) {
        @content;
    }
}
