.home-header {
  height: 90vh;
  background-color: #000000;
  position: relative;
  overflow: hidden;
  position: relative;
  
  @include _xl {
    background: url('../../../media/photos/stars.svg') no-repeat left center / cover ;
    background-color: #000000;
  }
  &-background-2 {
    position: absolute;
    width: 100vw;
    height: 140vh;
    top: -40%;
    left: 0;
    visibility: hidden;
    opacity: 0;
    animation: visible 2s;
    animation-delay: 2.2s;
    animation-fill-mode: forwards;
  }

  @keyframes visible {
    to {
      opacity: 1;
      visibility: visible;
    }
  }
  

  @import "./HomeHeaderTetris.scss";
  @import "./HomeScrollMouse.scss";
  @import "./HomeScrollArrow.scss";
  .scroll-thing {
	  visibility: hidden;
    opacity: 0;
    &.active {
		opacity: 1;
		visibility: visible;
		transition: 2s;
    }
  }
}


@import "./HomeAboutMe.scss";
@import './HomeWhatIDo.scss';
@import './HomeWorks.scss';